.notification {
  color: rgb(0, 255, 76);
  background: rgb(255, 255, 255);
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.error {
  color: rgb(252, 0, 0);
  background: rgb(255, 255, 255);
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
